<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data Drinks</h2>
                    <v-layout row wrap style="margin:10px">
                        <v-flex xs6>
                            <v-btn
                                depressed
                                dark
                                rounded
                                style="text-transform: none !important;"
                                color = "green accent-3"
                                @click="dialog = true">
                            
                                <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                                    Tambah Drinks
                            </v-btn>
                        </v-flex>
                    </v-layout>

                <v-row dense>
                    <v-col
                        v-for="(item) in drinks"
                        :key="item.id"
                        :cols="3">
                    
                        <v-card>
                            <v-card-title>{{ item.name }}</v-card-title>
                            <v-card-text class="text--primary">
                                <div>Categories: {{ item.categories }}</div>
                                <div>Calories: {{ item.calories }}</div>
                                <div>Fat: {{ item.fat }}</div>
                                <div>Carbs: {{ item.carbs}}</div>
                                <div>Protein: {{ item.protein}}</div>
                            </v-card-text>
                            <v-card-actions>
                                <v-btn icon class="mx-0" v-on:click="edit(item)">
                                    <v-icon class="mr-1">mdi-pencil</v-icon>
                                </v-btn>
                                <v-btn icon class="mx-0" v-on:click="hapus(item)">
                                    <v-icon class="mr-1">mdi-delete</v-icon>
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-col>
                </v-row>
            </v-container>
        </v-card>

        <v-dialog v-model="dialog" max-width="500px">
            <v-card>
                <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.name" label="Drinks name"></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                        <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>

  import {drinksRef} from '../firebase';

  export default {
    data: () => ({
      cek: -1,
      dialog: false,
      headers: [
      {
        text: 'Drinks (100g serving)',
        align: 'left',
        sortable: false,
        value: 'name',
      },
        { text: 'Categories', value: 'categories'},
        { text: 'Calories', value: 'calories' },
        { text: 'Fat (g)', value: 'fat' },
        { text: 'Carbs (g)', value: 'carbs' },
        { text: 'Protein (g)', value: 'protein' },
        { text: 'Actions', value: 'action', sortable: false },
        { text: 'Selections', value: 'select', sortable: false },
      ],
      drinks: [],
      type: ['Hot', 'Ice'],
      editedIndex: -1,
      editedItem: 
        {name: '',categories: '',calories: 0,fat: 0,carbs: 0,protein: 0,},
      defaultItem: 
        {name: '',categories: '',calories: 0,fat: 0,carbs: 0,protein: 0,},
      search: '',
      checked: [],
    }),
    
    firebase: {
      drinks: drinksRef
    },

    computed: {
      formTitle () {
        return this.cek === -1 ? 'New Item' : 'Edit Item'
      },
    },
    
    watch: {
      dialog (val) {
        val || this.close()
      },
    },
    
    methods: {
      close () {
        this.dialog = false
      },
      
      // methods untuk save
      
      save() {
        if (this.cek > -1) {
          drinksRef.child(this.editedIndex).set({name: this.editedItem.name,
            categories: this.editedItem.categories, 
            calories: this.editedItem.calories,
            fat: this.editedItem.fat, carbs: this.editedItem.carbs, 
            protein: this.editedItem.protein})
            this.clear()
        } else {
          drinksRef.push({name: this.editedItem.name,
            categories: this.editedItem.categories, 
            calories: this.editedItem.calories,
            fat: this.editedItem.fat, carbs: this.editedItem.carbs, 
            protein: this.editedItem.protein})
            this.clear()
          }
          this.close()
        },
      
      clear(){
        this.editedItem={}
        this.editedItem.name= ''
        this.editedItem.categories= ''
        this.editedItem.calories= 0
        this.editedItem.fat= 0
        this.editedItem.carbs= 0
        this.editedItem.protein= 0
        this.cek=-1
      },

      edit(item){
        this.editedIndex = item['.key']
        this.cek = 0
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      
      hapus(item){  
        confirm('Are you sure you want to delete this item?') && drinksRef.child((item['.key'])).remove()
      },

      multipleDelete(){
        for(var i=0; i<this.checked.length; i++){
          drinksRef.child(this.checked[i]).remove()
        }

        this.checked = []
      }
    },
  }
</script>